<template>
	<div id="JobList">
		<div class="header">
			<van-row gutter="20">
        <van-col span="4" style="padding-right: 0px !important;padding-left: 0px;">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="vertical-align: middle;" @click="$router.back(-1)"></i>
        </van-col>
				<van-col span="16" class='search font_12' style="background:#ffffff;">
          <van-search v-model="value" placeholder="搜索" @search="getList"/>
        </van-col>
        <van-col span="4" style="padding-left:0px !important;padding-right: 0px;">
          <!--<img src="static/img/icon_fbzp_zp.png" style="vertical-align: middle;width: 20px;" @click="toJobService"/>-->
        </van-col>
			</van-row>
		</div>
    <div class="body_box" style="padding: 5px">
      <div class="background_color_ffffff border_bottom_eeeeee" style="padding: 5px" v-for="(item,index) in list" @click="toJobInfo(item)">
        <img :src="item.user.userimg||'static/img/img_tx_zp.png'" style="width: 15%;vertical-align: text-top;"/>
        <div style="display: inline-block;width: 80%;">
          <span>{{item.title}}</span>
          <span class="font_12 float_right color_8b8b8b">发布时间:{{item.addtime}}</span>
        </div>
        <span style="position: relative;left: 17%;bottom: 20px;" class="font_12 color_8b8b8b">{{item.user.username}}</span>
        <div class="color_c10804 float_right font_12">¥{{item.price}}</div>
        <div class="color_40a3f3 text_right font_12">{{item.tell}}</div>
        <div style="display: inline-block;width: 100%;">
          <span class="font_12">{{item.content}}</span>
        </div>
      </div>
    </div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
        list:[],
        value:'',
			}
		},
		mounted() {
      this.getList();
		},
		methods: {
      getList(){
        let $this = this;
        let obj = {
          search:this.value,
          type: 6,
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/getsamecitylist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.list = rpdata.data.data.data;
          }
        })
        .catch(function(err) {})
      },
      toJobService(){
        this.$router.push('JobService')
      },
      toJobInfo(dataMap){
        this.$store.commit('setJobInfo', dataMap);
        this.$router.push('JobInfo');
      }
		}
	}
</script>

<style>
  #JobList .body_box{
    margin-top: 46px;
  }
  #JobList .van-search input {
    color: #8b8b8b;
  }
  #JobList .typeList {
    text-align: center;
  }
  #JobList .typeList span {
    font-size: 14px;
    color: rgb(102, 102, 102)
  }
  #JobList .typeImg {
    display: block;
    width: 75%;
    margin: 10px auto;
  }
	#JobList {
		width: 100%;
	}
	#JobList .header {
		height: 44px;
		line-height: 44px;
		box-shadow: 0px 2px 10px lightgrey;
		overflow: hidden;
		position: fixed;
		top: 0;
		width: 100%;
		z-index: 99;
		background-color: white;
	}
	#JobList .header img {
		vertical-align: text-top;
	}
	#JobList .search {
		border: 1px solid lightgray;
		border-radius: 30px;
		margin-top: 6px;
	}
	#JobList .header .van-col {
		text-align: center;
	}
	#JobList .van-search {
    padding: 0px;
  }
	#JobList .van-search {
		background-color: transparent!important;
	}
	#JobList .swiper img {
		margin: 0 auto;
		display: block;
	}
	#JobList .nav img {
		width: 100%;
		height: auto;
	}
	#JobList .navCon span {
		font-size: 13px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		color: white;
	}
	#JobList .rightText div {
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-size: 16px;
	}
	#JobList .footer .van-col div {
		text-align: center;
		font-size: 14px;
	}
	#JobList .swiperTwo .van-swipe__indicators{
		display: none;
	}
	#JobList .swiperTwo{
    margin-top: 44px;
	}
  #JobList .van-field__control {
    font-size: 12px;
  }
  .van-search .van-cell {
    padding: 3px 5px;
  }
</style>



